import React, { Component } from 'react';
//-->
//props

//
//valuereact
//
export default class Todoinput extends Component {
    constructor(){
        super();
        this.state = {
            val:''
        }
    }
    handleInput = (e)=>{
        if(e.keyCode === 13){
            this.props.addTodo(e.target.value);
            this.state.val='';
        }
    }
    handleChange = (e)=>{
        this.setState({
            val:e.target.value
        })
    }
    render() {
        return (
            <div>
                <h1>ToDoList</h1>
                <input id="inp" name="val" placeholder="ToDo" onChange={(e)=>this.handleChange(e)} value={this.state.val} onKeyDown={(e)=>this.handleInput(e)} type="text"/>
            </div>
        )
    }
}

